.relatedproducts{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 90vh;

}
.relatedproducts h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;

}
.relatedproducts hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.relatedproducts-items{
    margin-top: 50px;
    display: flex;
    gap: 20px;
    object-fit: cover;
    width: 100%;
    padding: 30px 40px;
 
}
