.item{
    width: 100%;
    text-align: center;
}
.item p{
    margin: 6px 0px;
}
.item-prices{
    display: flex;
    gap: 20px;
}
.item img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 8px; /* optional for styling */
}
.item-prices-new{
    color: #374151;
    font-size: 18px;
    font-weight: 600;
}
.item-prices-old{
    color: #8c8c8c;
    font-size: 18px;
    font-weight: 500;
    text-decoration: line-through;
}
.item:hover{
    transform: scale(1.05);
    transition: 0.6s;

}